/* Button Colors and Styles */

.btn--primary--solid {
  background-color: #39a9db;
  background-color: var(--primary);
  color: #fdfdfd;
  color: var(--white);
  border: none;
}

.btn--warning--solid {
  background-color: #f9dc5c;
  background-color: var(--warning);
  color: #181717;
  color: var(--dark);
  border: none;
}

.btn--danger--solid {
  background-color: #e84855;
  background-color: var(--danger);
  color: #fdfdfd;
  color: var(--white);
  border: none;
}

.btn--success--solid {
  background-color: #abe188;
  background-color: var(--success);
  color: #fdfdfd;
  color: var(--white);
  border: none;
}

.btn--primary--outline {
  background-color: transparent;
  border: 2px solid #39a9db;
  border: 2px solid var(--primary);
  color: #39a9db;
  color: var(--primary);
}

.btn--warning--outline {
  background-color: transparent;
  border: 2px solid #f9dc5c;
  border: 2px solid var(--warning);
  color: #f9dc5c;
  color: var(--warning);
}

.btn--danger--outline {
  background-color: transparent;
  border: 2px solid #e84855;
  border: 2px solid var(--danger);
  color: #e84855;
  color: var(--danger);
}

.btn--success--outline {
  background-color: transparent;
  border: 2px solid #abe188;
  border: 2px solid var(--success);
  color: #abe188;
  color: var(--success);
}

/* Button Size */

.btn--medium {
  padding: 10px 20px;
  font-size: 18px;
